@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
#notistack-snackbar{max-width:400px;overflow:hidden;flex-wrap:wrap}#app-root{width:100%;height:100%}html,body{width:100%;height:100%;margin:0}.skelton{flex-grow:1}.skelton span{border-radius:4px;width:100%}

body::-webkit-scrollbar {
    width: 8px;
}

body::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

body::-webkit-scrollbar-thumb {
    background: rgb(122, 110, 170);
    border-radius: 8px;
}

.MuiDrawer-paper::-webkit-scrollbar {
    width: 0px;
}

.text-center {
    text-align:center;
}
